@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
/*       Fonts       */
/*       Colors       */
html {
  font-size: 16px;
}

/*      Main content        */
.module {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
}
.module h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 2.625rem;
  color: #121212;
  line-height: 50.4px;
}
.module p {
  font-family: "Roboto", sans-serif;
  color: #121212;
  line-height: 24px;
}
.module .mainContent {
  display: flex;
  margin-top: 96px;
  margin-left: auto;
  margin-right: auto;
}
.module .mainContent .rightSide {
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 95px;
  max-width: 380px;
}
@media (orientation: portrait) {
  .module .mainContent {
    flex-direction: column;
    width: 80%;
  }
  .module .mainContent .rightSide {
    margin-left: auto;
    margin-right: auto;
  }
  .module .mainContent picture {
    margin-left: auto;
    margin-right: auto;
  }
  .module .mainContent button {
    margin-left: auto;
    margin-right: auto;
  }
}

/*      Alert       */
.module .alertBackground {
  display: none;
  position: absolute;
  background-color: rgba(18, 18, 18, 0.5);
  width: 100%;
  height: 100%;
  inset: 0;
  z-index: 1;
}
@media (orientation: portrait) {
  .module .alertBackground {
    overflow: scroll;
  }
}
@media screen and (orientation: landscape) and (max-height: 900px) {
  .module .alertBackground {
    overflow: scroll;
  }
}
.module .alertBackground .alertContent {
  min-width: 40%;
  position: absolute;
  background-color: #FFF;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 46px 46px 56px 95px;
}
.module .alertBackground .alertContent h1 {
  margin: 0;
}
.module .alertBackground .alertContent .table {
  display: none;
}
.module .alertBackground .alertContent .loadSection {
  display: none;
}
@media (orientation: portrait) {
  .module .alertBackground .alertContent {
    min-width: 60%;
    max-width: 80%;
    padding: 12px 12px 16px 20px;
  }
}
@media screen and (orientation: portrait) and (max-width: 430px) and (max-height: 700px) {
  .module .alertBackground .alertContent {
    margin-top: 120px;
  }
}
@media screen and (orientation: portrait) and (max-width: 430px) and (min-height: 700px) {
  .module .alertBackground .alertContent {
    margin-top: 80px;
  }
}
@media (orientation: portrait) {
  .module .alertBackground .alertContent .table {
    overflow: scroll;
  }
}
@media screen and (orientation: landscape) and (max-height: 800px) {
  .module .alertBackground .alertContent {
    margin-top: 70px;
  }
}
@media screen and (orientation: landscape) and (max-height: 700px) {
  .module .alertBackground .alertContent {
    margin-top: 100px;
  }
}
@media screen and (orientation: landscape) and (max-height: 600px) {
  .module .alertBackground .alertContent {
    margin-top: 140px;
  }
}

/*      Buttons     */
.generalBtn {
  font-family: "Roboto", sans-serif;
  background-color: #E4C1AE;
  color: #FFF;
  border-radius: 32px;
  border-width: 0px;
  width: 128px;
  height: 48px;
  font-weight: 700;
  font-size: 1rem;
  line-height: 16px;
}
.generalBtn:hover {
  outline: 1px, solid, #121212;
  color: #e6e6e6;
}
.generalBtn:active {
  background-color: #d9a78c;
  color: #bfbfbf;
  outline: 1px, solid, #121212;
}

.closeButton {
  color: #121212;
  font-size: 2rem;
  font-weight: 700;
  text-align: right;
  margin-block-start: 0;
  margin-block-end: 0;
  position: absolute;
  top: 46px;
  right: 46px;
  width: auto;
  box-shadow: none;
  border: none;
  background-color: #FFF;
}
.closeButton:hover {
  color: #bfbfbf;
}

#resetCountBtn {
  display: none;
}